.about-bg {
  background-color: #bfe0ff;
  padding: 40px 0;
}

.about-section {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 73vh;
  display: flex;
}

.about-text {
  text-align: center;
  max-width: 800px;
}

.about-text h2 {
  color: #333;
  margin-bottom: 20px;
  font-size: 32px;
}

.about-text p {
  color: #555;
  font-size: 20px;
  line-height: 1.6;
}

.about-text ul {
  margin-top: 20px;
  list-style-type: none;
}

.about-text li {
  margin-bottom: 10px;
}

.about-text li strong {
  color: #333;
  font-weight: bold;
}

.galleryContainer {
  justify-content: center;
  display: flex;
}

.gallery {
  width: 100%;
  margin-inline: auto;
}

.gallery__picture {
  margin-inline: auto;
}

.gallery__grid {
  --c: 7;
  --r: 6;
  --g: 4px;
  --s: 100px;
  --f: 1.5;
  width: calc(var(--c) * var(--s)  + calc(var(--c)  - 1) * var(--g));
  height: calc(var(--r) * calc(var(--s) * .5)  + calc(var(--r)  - 1) * var(--g));
  aspect-ratio: 1;
  grid-template-columns: repeat(var(--c), auto);
  grid-gap: var(--g);
  margin-inline: auto;
  display: none;
}

.gallery__grid__item {
  object-fit: cover;
  border-radius: 20px;
  width: 0;
  min-width: 100%;
  height: 0;
  min-height: 100%;
  transition: all .25s ease-in-out;
  overflow: hidden;
}

.gallery__grid__item:hover, .gallery__grid__item:focus {
  cursor: pointer;
  filter: grayscale(0);
  width: calc(var(--s) * var(--f));
  height: calc(var(--s) * var(--f));
}

.gallery__grid__item:first-child {
  grid-row: 3 / 5;
}

.gallery__grid__item:nth-child(2) {
  grid-row: 2 / 4;
}

.gallery__grid__item:nth-child(3) {
  grid-area: 1 / 3 / 5 / 5;
}

.gallery__grid__item:nth-child(4) {
  grid-row: 2 / 4;
}

.gallery__grid__item:nth-child(5) {
  grid-row: 2 / 4;
  position: relative;
  top: -25px;
}

.gallery__grid__item:nth-child(6) {
  grid-area: 3 / 7 / 5;
}

.gallery__grid__item:nth-child(7) {
  grid-row: 4 / 6;
}

.gallery__grid__item:nth-child(8) {
  grid-area: 5 / 3 / 7 / 4;
}

.gallery__grid__item:nth-child(9) {
  grid-row: 4 / 6;
}

.gallery__grid__item:nth-child(10) {
  grid-row: 4 / 6;
  position: relative;
  top: -25px;
}

@media (min-width: 80em) {
  .gallery__picture {
    display: none;
  }

  .gallery__grid {
    display: grid;
  }
}

